@import url('https://fonts.googleapis.com/css2?family=PT+Sans&family=Teko:wght@300;400;500;600;700&display=swap');

.App {
  text-align: center;
}

.App-header {
  background-color:#444444;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

.Nav{
  background-color: #2c2d30;
  min-height: 40px;
  height: 50px;
  display: flex;
  flex-direction: flex-start;
  align-items: center;
  justify-content: center;
  width: 100vw;
  position: fixed;
  font-family: 'PT Sans';
  font-size: 1.5em;
  font-weight: 300;
}

.Nav a {
  color: white;
  text-decoration: none;
  margin: 0px 25px;
}

.App-header h1{
  font-family: Teko;
  font-weight: 300;
  font-size: 5em;
}